@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap');
*{
  font-family: 'Roboto', sans-serif;
}
body{
  background: linear-gradient(85deg, rgba(2,0,36,1) 0%, rgba(121,9,9,1) 42%, rgba(0,212,255,1) 100%);

}
.container{
  width: 1000px;
  height: auto;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
}

.form{
  width: 400px;
  height: 250px;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: #00b4d8;
  margin: 0 auto;
margin-top: 50px;
padding-top: 50px;
border-radius: 5px;
}
.inputs{
  font-weight: 600;
  width: 220px;
  padding-top: 7px;
  padding-bottom: 7px;
  margin-bottom: 10px;
  color: #000;
  font-size: 16px;
  border-radius: 5px;
  text-align: center;
}
.inputs::placeholder{
  font-weight: 600;
  color: #000;
  font-size: 16px;
  opacity: 0.7;
}
.btn{
  font-weight: 500;
  padding-top: 5px;
  padding-bottom: 5px;
  color: #003049;
  padding-left: 40px;
  padding-right: 40px;
  border-radius: 5px;
  border: none;
  font-size: 16px;
}
.box{
  text-align: center;
box-shadow: 0px 3px 2px rgba(0, 0, 0, 0.1);
  margin: 0 auto;
  width: 500px;
}
.box-2{
  margin: 0 auto;
  border-radius: 5px;
  width: 300px;
  height: 170px;
  background: rgb(195,34,34);
  background: linear-gradient(90deg, rgba(195,34,34,1) 0%, rgba(255,166,5,1) 100%);
  margin-top: 30px;
  margin-bottom: 30px;
  padding-top: 5px;
}
.box-2 h4, .box-2 h3{
  margin: 0;
  padding: 0;
  padding: 7px;
  color: #fff;
}
.btn-2{
  font-weight: 600;
  font-size: 14px;
  margin: 0 auto;
  font-weight: 500;
  color: #0D75FF;
  padding-top: 3px;
  padding-bottom: 3px;
  padding-left: 15px;
  padding-right: 15px;
  border-radius: 5px;
  border: none;
  background-color: #fff;
  margin-top: 5px;
  margin-left: 10px;
}
.btn-3{
  font-weight: 600;
  font-size: 14px;
  margin: 0 auto;
  font-weight: 500;
  color: #0D75FF;
  padding-top: 3px;
  padding-bottom: 3px;
  padding-left: 15px;
  padding-right: 15px;
  border-radius: 5px;
  border: none;
  background-color: #fff;
  margin-top: 5px;
  margin-left: 10px;
}
.box-3{
  margin: 0 auto;
  border-radius: 5px;
  width: 300px;
  height: 170px;
  background: rgb(195,34,34);
  background: linear-gradient(90deg, rgba(195,34,34,1) 0%, rgba(255,166,5,1) 100%);
  margin-top: 30px;
  margin-bottom: 30px;
  padding-top: 5px;
}
.box-3 input {
  text-align: center;
  margin: 7px;
  border-radius: 5px;
  font-weight: 600;
  color: #000;
  font-size: 16px;
  opacity: 0.7;
}